import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/news-content';

export const apiConferenceGet = async (serviceUrl, catgeoryCode, contentTypeCode) => {
  const url = `${serviceUrl}/${resource}`;
  const urlParameters = new URLSearchParams({
    category: catgeoryCode,
    contentType: contentTypeCode
  });
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url + '?' + urlParameters, options);
};
