import React from 'react';
import ReactDOM from 'react-dom';
import App from '../App';

import KeycloakContext from '../auth/KeycloakContext';
import { subscribeToWidgetEvent } from '../helpers/widgetEvents';
import { KEYCLOAK_EVENT_TYPE } from '../widgetEventTypes';
import setLocale from '../i18n/setLocale';

const getKeycloakInstance = () =>
  (window &&
    window.entando &&
    window.entando.keycloak && { ...window.entando.keycloak, initialized: true }) || {
    initialized: false,
  };

const ATTRIBUTES = {
  serviceUrl: 'service-url',
  locale: 'locale',
  categoryCode: 'category-code',
  contentTypeCode: 'content-type-code',
  widgetTitle: 'widget-title'
};

class CardWidget extends HTMLElement {
  constructor(...args) {
    super(...args);

    this.mountPoint = null;
    this.unsubscribeFromKeycloakEvent = null;
    this.keycloak = getKeycloakInstance();
  }

  connectedCallback() {
    this.mountPoint = document.createElement('div');
    this.appendChild(this.mountPoint);

    this.keycloak = { ...getKeycloakInstance(), initialized: true };

    this.unsubscribeFromKeycloakEvent = subscribeToWidgetEvent(KEYCLOAK_EVENT_TYPE, () => {
      this.keycloak = { ...getKeycloakInstance(), initialized: true };
      this.render();
    });

    this.render();
  }

  render() {
    const customEventPrefix = 'news.updates.';

    const serviceUrl = this.getAttribute(ATTRIBUTES.serviceUrl) || '';

    const onError = error => {
      const customEvent = new CustomEvent(`${customEventPrefix}error`, {
        details: {
          error,
        },
      });
      this.dispatchEvent(customEvent);
    };

    const locale = this.getAttribute(ATTRIBUTES.locale);
    setLocale(locale);

    const contentTypeCode = this.getAttribute(ATTRIBUTES.contentTypeCode);
    const categoryCode = this.getAttribute(ATTRIBUTES.categoryCode);
    const widgetTitle = this.getAttribute(ATTRIBUTES.widgetTitle);

    const ReactComponent = React.createElement(App, {
      contentTypeCode,
      categoryCode,
      widgetTitle,
      onError,
      serviceUrl,
    });
    ReactDOM.render(
      <KeycloakContext.Provider value={this.keycloak}>{ReactComponent}</KeycloakContext.Provider>,
      this.mountPoint
    );
  }

  disconnectedCallback() {
    if (this.unsubscribeFromKeycloakEvent) {
      this.unsubscribeFromKeycloakEvent();
    }
  }
}
if (!customElements.get('card-widget')) {
  customElements.define('card-widget', CardWidget);
}

export default CardWidget;